(function ($) {
  $(document).ready(function () {

    // $('.acf-row:not(:last-child)').before('<tr class="minimize-row"><td colspan="3"><div class="expand"></div></td></tr>');
    //
    // $('body').on('click', '.expand', function() {
    //   $(this).parent().parent('.minimize-row').next('.acf-row').toggleClass('minimize');
    //   $(this).toggleClass('minimize');
    //   console.log('hjello');
    // });


    /* Handle image field */

    function acfInputField (data_attr) {
      return '.acf-field[data-name="'+data_attr+'"] input';
    }

    function handleImageTitle (ev) {
        var val = $(this).val();
        var parent = $(this).closest('.acf-row');
        var imgBox = parent.find('.show-if-value');

        var contentBox = imgBox.find('.hero-content');

        if (contentBox.length === 0) {
          imgBox.prepend('<div class="hero-content"></div>');
          contentBox = imgBox.find('.hero-content');
        }

        if (imgBox.length !== 0) {
          var textBox = imgBox.find('.image-section-title');
          if (textBox.length === 0) {
            contentBox.prepend('<h2 class="image-section-title"></h2>');
          }
          textBox = imgBox.find('.image-section-title');
          textBox.text(val);
        }
    }

    function handleImageText (ev) {
        var val = $(this).val();
        var parent = $(this).closest('.acf-row');
        var imgBox = parent.find('.show-if-value');

        var contentBox = imgBox.find('.hero-content');

        if (contentBox.length === 0) {
          imgBox.prepend('<div class="hero-content"></div>');
          contentBox = imgBox.find('.hero-content');
        }

        if (imgBox.length !== 0) {
          var textBox = imgBox.find('.image-section-text');
          if (textBox.length === 0) {
            contentBox.prepend('<p class="image-section-text"></p>');
          }
          textBox = contentBox.find('image-section-text');
          textBox.text(val);
        }
    }

    $('#section-editor').on('keyup', acfInputField('image_title'), handleImageTitle);
    $('#section-editor').on('keyup', acfInputField('image_text'), handleImageText);

    // Also do it on load
    $('#section-editor ' + acfInputField('image_title')).each(handleImageTitle);
    $('#section-editor ' + acfInputField('image_text')).each(handleImageText);

  });
})(window.jQuery);
